.race {
  width: 100% !important;
  overflow: hidden;
}

.row {
  max-height: 750px;
}

.rowLink {
  color: black;
  display: block;
}

.rowLink:hover {
  color: black;
}

tr.row-highlight:hover,
td.cell-highlight:hover {
  background-color: lightsteelblue !important;
}

.column {
  display: inline-block !important;
}

.scroll {
  overflow: auto;
  white-space: nowrap !important;
  flex-wrap: nowrap !important;
}

.runningLine {
  margin-right: 8px !important;
}
